import { Stack } from "@mui/material";
import { Box, styled } from "@mui/material";
import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';

const Container = styled("div")(({ theme }) => ({
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
        marginBottom: "30px",
        [theme.breakpoints.down("sm")]: { marginBottom: "16px" }
    }
}));

export default function Notification()
{
    const localizer = momentLocalizer(moment);
    const today = new Date();
    return (
        <Container>
            <Box className="breadcrumb">
                <Breadcrumb routeSegments={[{ name: "매장관리", path: "/material" }, { name: "구인 캘린더" }]} />
            </Box>

            <Stack spacing={3}>
                <SimpleCard title="구인캘린더 페이지">
                    <Calendar
                        titleAccessor={'title'}
                        startAccessor={'start_time'}
                        endAccessor={'end_time'}
                        selectable
                        scrollToTime={new Date(1970, 1, 1, 6)}
                        localizer={localizer}

                        defaultView="month"
                        views={['month', 'week']}
                        style={{ height: '75vh' }}
                        defaultDate={new Date()}

                        min={new Date(today.getFullYear(), today.getMonth(), today.getDate(), 7)}
                        max={new Date(today.getFullYear(), today.getMonth(), today.getDate(), 22)}
                    />
                </SimpleCard>



            </Stack>
        </Container>
    );
}
